html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Anaktoria';
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wedding-site {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.fullscreen-section {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section1 {
  background-color: #fff9f2;
  background-image: url('./Assets/Homeee2.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  /* width: 90%; */
  /* Add any other styles for the first section */
}

.section9 {
  background-image: url('./Assets/gennaro.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  /* height: 30vh; */
  /* margin: 10px; */
}


.section2 {
  background-position: center;
  background-size: cover;
  background-color: #ddd8c5;
  /* background-color: #fff9f2; */
  position: relative;
}

.section3 {
  /* background-color: #fff9f2; */
  background-color:  #ddd8c5;
  position: relative;
  /* border: double; */
}

.left-content {
  text-align: center;
  color: #c09c70;
  background-color: #fff;
  /* display: flex; */
  /* flex: 1; */
  margin: 20px;
  border: solid #c4a274 2px;
  flex-direction: column;
  flex-wrap: wrap;
}

.right-map-content {
  position: inherit;
  text-align: center;
  color: #c09c70;
  flex: 1;
  padding-right: 20px;
}

p {
  font-size: 1.4rem;
}

.right-gif {
  width: 35%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('./Assets/wedding.gif');
}

.logo-final {
  width: 35%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('./Assets/logo.png');
}

.image-top-left,
.image-bottom-left,
.image-top-left-s3,
.image-bottom-right-s3,
.image-bottom-left-s3,
.image-top-left-s4,
.image-top-right-s7,
.image-bottom-left-s7,
.image-top-left-s7,
.image-bottom-left-s7 {
  position: absolute;
  width: 200px;
  height: 200px;
  background-size: contain;
  background-repeat: no-repeat;
  
}

.image-top-left {
  background-image: url('./Assets/RSVP-1.png');
  top: 0;
  left: 0;
}

.image-top-left-s7 {
  background-image: url('./Assets/RSVP-1.png');
  top: 0;
  left: 0;
}

.image-bottom-left {
  background-image: url('./Assets/RSVP-2.png');
  bottom: 0;
  left: 0;
}

.image-bottom-left-s7 {
  background-image: url('./Assets/RSVP-2.png');
  bottom: 0;
  left: 0;
}

.image-top-left-s3 {
  background-image: url('./Assets/S3-4.png');
  top: 0;
  left: 0;
  width: 500px;
  height: 500px;
}

.image-bottom-left-s7 {
  background-image: url('./Assets/S3-3.png');
  bottom: 0;
  left: 0;
  width: 400px;
  height: 400px;
}

.image-bottom-left-s3 {
  background-image: url('./Assets/S3-3.png');
  bottom: 0;
  left: 0;
  width: 400px;
  height: 400px;
}

.image-top-right-s7 {
  background-image: url('./Assets/S7.png');
  top: 0;
  right: 0;
  width: 400px;
  height: 400px;
}

.image-bottom-right-s3 {
  background-image: url('./Assets/S3-5.png');
  /* background-image: url('./Assets/RSVP-2-full.png'); */
  bottom: 0;
  right: 0;
  width: 250px;
  height: 250px;
}

.image-top-left-s4 {
  background-image: url('./Assets/S4-1.png');
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


.center-images {
  display: flex;
  justify-content: center;
  position: relative;
  bottom: 100px;
}

.image-container {
  width: 500px;
  height: 300px;
  margin: 10px;
  box-sizing: border-box;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.s3-left-text {
  position: absolute;
  left: 200px;
  top: 400px;
  line-height: 20px;
  text-align: center;
  color: #c09c70;
  flex: 1;
  font-size: 1.4rem;
}

/* Additional styles for images (Replace with your actual image paths) */
.image1 {
  background-image: url('./Assets/S3-1.png');
}

.image2 {
  background-image: url('./Assets/S3-2.png');
}

.section4 {
  background-position: center;
  background-size: cover;
  background-color: #ddd8c5;
  position: relative;
  height: auto;
}

/* unvisited link */
a:link {
  color: #fff;
}

/* visited link */
a:visited {
  color: #fff;
}

/* mouse over link */
a:hover {
  color: #fff;
}

/* selected link */
a:active {
  color: #fff;
}

.map {
  height: 400px;
  width: 100%;
}

.full-screen-image {
  position: relative; /* Position the image absolutely within the section */
  top: 0;
  left: 0;
  width: 100%; /* Make the image fill the entire width of the section */
  height: 100%; /* Make the image fill the entire height of the section */
  object-fit: contain; /* Resize the image to cover the entire section */
  object-position: center; /* Center the image within the section */
}

.section5 {
  background-color: #fff9f2;
}

.section6 {
  background-color: #fff;
}

.left-6 {
  width: 40%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('./Assets/S6-1.png');
}

.inner-section-6 {
  height: 100vh; /* Set the section height to full viewport height */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: #fff9f2; */
}

.japan-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.japan-image-container {
  max-height: calc(100vh - 2rem - 2px); /* Adjust as needed to account for padding or borders */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  /* background-color: #fff9f2; */
}

.section7 {
  flex-direction: column;
  /* height: auto; */
  position: relative;
  background-color: #ddd8c5;
}

.image-section-7 {
  width: 100%;
  height: 40vh;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('./Assets/logo.png');
}

.content-s7 {
  text-align: center;
  color: #fff;
  flex: 1;
  padding: 20px;
  margin: 20px;
}

.rotating-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  animation: rotate 120s linear infinite; /* Adjust the duration and timing function as needed */
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Mobile styles */
@media (max-width: 768px) {

  .section1 {
    background-image: url('./Assets/Homeee2.png');
    background-size: contain;
    background-color: #fff9f2;
  }

  .section2 {
    flex-direction: column;
    height: auto;
  }

  .section6 {
    flex-direction: column;
    height: auto;
  }

  .section3 {
    flex-direction: column;
    height: auto;
  }

  .section4 {
    flex-direction: column;
    height: auto;
  }

  .left-content {
    order: 2;
  }

  .content-s7 {
    order: 2;
  }

  .inner-section-6 {
    order: 2;
  }

  .right-gif {
    width: 90%;
    height: 50vh;
    order: 3;
  }

  .logo-final {
    width: 55%;
    height: 40vh;
    order: 4;
  }

  .left-6 {
    width:100%;
    height: 70vh;
    order: 1;
  }

  .right-map-content {
    flex-basis: 100%;
    padding-right: 0;
    margin-top: 5vh;
    line-height: 20px;
    order: 3;
  }

  .image-bottom-left {
    width: 200px;
    height: 200px;
    opacity: 0.5;
  }

  .image-top-left {
    display: none;
  }

  .image-bottom-left-s7 {
    display: none;
  }

   .image-top-left-s7 {
    display: none;
  }

  .image-bottom-left-s3,
  .image-bottom-right-s3 {
    opacity: 0.15;
  }

  .map {
    height: 400px;
    /* width: 400px; */
  }

  .section5 {
    height: 140vh !important;
  }

  .image-top-left-s4 {
    background-image: url(./Assets/S4-2.png);
    bottom: 0 !important;
    right: 0 !important;
    height: 70vh;
    width: 70vh;
    top: auto;
    left: auto;
  }
}


@media (max-width: 1050px) {
  .center-images {
    flex-direction: column;
    align-items: center;
    top: 0;
    margin-top: 5vh;
  }

  .image-container {
    height: 260px;
    width: 400px;
  }

  .s3-left-text {
    position: relative;
    left: 0;
    top: 0;
    flex: 0;
  }

  .image-top-left-s3,
  .image-bottom-left-s3 {
    display: none;
  }

  .image-bottom-left-s7 {
    opacity: 0.4;
    height: 300px;
    width: 300px;
  }

  .image-top-right-s7 {
    display: none;
  }
}

@media (max-width: 500px) {

  .image-container {
    height: 200px;
    width: 350px;
  }

  .map {
    height: 300px;
    /* width: 300px; */
  }
}
